@import '../Styles/variables.scss';

.fade-up {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
    &.visible {
        opacity: 1;
        transform: translateY(0);
      }
    
  }
  
  .fade-in {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 1s ease-in-out, transform 1s ease-in-out; // transform 추가
    &.visible {
        opacity: 1 !important;
        transform: translateY(0);
    }
}
.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    // gap: 3rem;
    font-family: 'NanumSquare', sans-serif; 
    .service_area {
        min-height: 20rem;
        display: flex;
        flex-direction: column;
        text-align: left;
        padding: 5vw;
        gap: 1rem;
        font-size: 2.2rem;
        background-color: #ffffffa2;

        >p {
            font-size: 1.8rem;
            margin-bottom: 2rem;
            transition-delay: 0.2s;
            transition: opacity 0.5s ease, transform 0.5s ease;
            font-weight: 600;
            color: $FONT_COLOR;

        }
        >b {
            transition-delay: 0.6s; 
            transition: opacity 0.5s ease, transform 0.5s ease;
            font-weight: 800;

        }
        >span {
            font-size: 1.6rem;
            display: flex;
            align-items: center;
            gap: 0.5rem;
            transition-delay: 0.6s; 
            transition: opacity 0.5s ease, transform 0.5s ease;

            >b {
                font-size: 2.2rem;
                font-weight: 800;
            }
        }
        >div {
            
        }
    }
    .tit_name {
        color: #ffffffbe;
        font-size: 11rem;
        text-align: right;
        display: flex;
        justify-content: flex-end;
    }

    .process_sec {
        background-color: #90a39d;
        height: 100%;
        width: 100%;
        padding: 4rem 0;
        color: $WHITE;
        .process {
            width: 90%;
            margin: 0 auto;
            border-radius: 1rem;
            padding: 4rem 2rem;
            overflow-x: auto;
            // background-color: $BLUE_200;
        }
    }

}
.estimate {
    width: 100%;
    height: 100%;
    background-color: $WHITE;
    margin: 10vh 0;
    padding: 5vh 0;
    font-family: 'NanumSquare', sans-serif; 

    .tit {
        padding: 2rem 0;
        font-size: 1.6rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        .main_tit {
            display: flex;
            flex-direction: column;
            text-align: left;
            margin: 0 auto;
            font-weight: 600;
        }
        .body_tit {
            font-size: 0.9rem;
        }
    }
    .box {
        margin: 3rem 0;
        .quast_box {
            background-color: $FONT_COLOR;
            color: $WHITE;
            max-width: 70%;
            padding: 2rem 3rem;
            margin: 0 auto;
            border-radius: 10rem;
            font-size: 2.2rem;
        }
        .quast_info {
            margin: 2rem;
        }
        .answer_box {
            margin: 3rem auto;
            display: flex;
            justify-content: center;
            gap: 2rem;
            flex-wrap: wrap;
            max-width: 55%;

            >button {
                padding: 1rem 2rem;
                border-radius: 3rem;
                font-size: 1.2rem;
                font-weight: 600;
                color: #000;
                width: fit-content;
            }
        }
        .result {
            display: flex;
            justify-content: center;
            flex-direction: column;
            gap: 4rem;
            .total {
                font-size: 1.6rem;
                >b{
                    font-size: 2rem;
                    border-bottom: 10px solid $BLUE_400;
                    transform: translateY(-5px);
                }
            }
            .total_info {
                font-size: 0.8rem;
            }
            button {
                padding: 0.7rem 3rem;
                width: fit-content;
                border-radius: 10px;
                font-size: 1.3rem;
                font-weight: 700;
                color: $WHITE;
                &.restart_btn {
                    margin: 0 auto;
                    background-color: $BLUE_300;
                }
            }
            .btn_set {
                display: flex;
                justify-content: space-around;
                width: 70%;
                margin: 0 auto;

                >button {
                    // color: $BLUE_800;
                    border-radius: 0;
                    padding: 1rem;
                    width: 50%;
                    color: $BLUE_800;
                    font-weight: 600;
                    
                    &.call_btn {
                        background-color: $BLUE_200;
                    }
                    &.sms_btn {
                        background-color: $BLUE_100;
                    }
                }
            }
        }
    }
}


@media screen and (max-width: 769px) {
    .main {
        .service_area {
            p {
                font-size: 1.3rem;
            }
            b {
                font-size: 1.5rem;
            }

            >span {
                font-size: 1rem;
                b {
                    font-size: 1.5rem;
                }
    
            }
        }    
        .tit_name {
            font-size: 18vw;
            text-align: right;
            display: flex;
            justify-content: flex-end;
        }
    }
    .estimate {
        margin-bottom: 0 !important;
        padding: 5vw 0 1vw;
        .tit {
            
            .main_tit {
                font-size: 1.6rem;
                padding: 0 2rem;
            }
            .body_tit {
                font-size: 0.8rem;
            }
        }
        .box {
            .quast_box {
                max-width: 90%;
                padding: 5vw 2vw;
                font-size: 1.1rem;
                margin: 0 auto !important;
            }
            .quast_info {
                font-size: 0.8rem;
            }

            .answer_box {
                max-width: 100%;
                gap: 1rem;
                padding: 0;
                margin: 1rem auto;
                >button {
                    padding: 1rem 2rem;
                    font-size: 1rem;
                }
            }
        }
        .result {
            gap: 3rem !important;
            .btn_set {
                width: 100% !important;
                >button {
                    font-size: 1rem;
                }
            }
        }
  }
}
